import React, { Component } from 'react';

import ProductSearch from './product-search/containers/product-search'
import ProductDetail from './product-detail/containers/product-detail'

class App extends Component {
	constructor(props) {
		super(props)

		this.state = {
			producto: false
		}

		this.producto = this.producto.bind(this)
		this.borrarProducto = this.borrarProducto.bind(this)
	}

	producto(producto) {
		this.setState({
			producto
		});
	}

	borrarProducto() {
		this.setState({
			producto: false
		});
	}

	render() {
		return (
			<div className="App">
				{!this.state.producto &&
					<ProductSearch 
						producto={this.producto} />
				}

				{this.state.producto &&
					<ProductDetail 
						borrarProducto={this.borrarProducto}
						producto={this.state.producto} />
				}
			</div>
		);
	}
}

export default App;

