import React from 'react';
import Quagga from 'quagga'
import Webcam from "react-webcam";

import Layout from '../components/scanner-layout'

var videoConstraints = {}
if (process.env === 'development') {
    videoConstraints = {
        width: {min: 640},
        height: {min: 480},
        facingMode: "environment",
        aspectRatio: {min: 1, max: 2}
    }
} else {
    videoConstraints = {
        width: 640,
        height: 480,
        facingMode: "environment",
    }
}

class Scanner extends React.Component {
    setRef = webcam => {
        this.webcam = webcam;
    };

    constructor(props) {
        super(props);

        this.state = {
            codigo: false
        }

        this.getScreenshot = this.getScreenshot.bind(this)
    }

    scanImage(dataUri) {
        Quagga.decodeSingle({
            decoder: {
                readers: ["ean_reader"],
            },
            locate: true,
            src: dataUri
        }, function(result) {
            if(result && result.codeResult) {
                this.props.onSuccess(result.codeResult.code)
            } else {
                this.props.onFail({
                    error: "NOT_DECODED"
                })
            }
        }.bind(this));
    }

    getScreenshot() {
        if (this.webcam.getScreenshot()) {
            this.scanImage(this.webcam.getScreenshot())
        } else {
            this.props.onFail({
                error: "NOT_CAPTURED"
            })
        }
    }

    render() {
        return (
            <Layout>
                <Webcam
                    heigh={480}
                    width={480}
                    audio={false}
                    ref={this.setRef}
                    screenshotFormat="image/jpeg"
                    videoConstraints={videoConstraints}
                />
                <button type="button" className="btn btn-primary" onClick={this.getScreenshot}>Escanear</button>
            </Layout>
        );
    }
}

export default Scanner;