import React from 'react';

import './product-detail.css';

function ProducDetailLayout(props) {
	return(
		<div className="product-detail-wrapper">
			<div className="card">
				<img alt={props.producto.nombre} className="card-img-top" src={`https://imagenes.preciosclaros.gob.ar/productos/${props.producto.id}.jpg`} />
				<div className="card-body">
					<h5 className="card-title">{props.producto.nombre}</h5>
					<p className="card-text">{props.producto.marca}</p>
					<p><small>Precio Minimo</small> <br/>{props.producto.precioMin}</p>
					<p><small>Precio Máximo</small> <br/>{props.producto.precioMax}</p>
					<button className="btn btn-primary" onClick={props.nuevoEscaneo}>Escanear otro</button>
				</div>
			</div>
		</div>
	)
}

export default ProducDetailLayout;