import React, { Component } from 'react';
import Layout from '../components/product-detail-layout'

class ProductDetail extends Component {
	constructor(props) {
		super(props)
		this.nuevoEscaneo = this.nuevoEscaneo.bind(this)
	}

	nuevoEscaneo() {
		this.props.borrarProducto()
	}

	render() {
		return (
			<Layout 
				nuevoEscaneo={this.nuevoEscaneo}
				{...this.props.producto} 
				/>
		);
	}
}

export default ProductDetail;