import React, { Component } from 'react';

import Api from '../../utils/api';
import Scanner from '../../scanner/containers/scanner.js';

class ProductSearch extends Component {
	constructor(props) {
		super(props)

		this.onSuccess = this.onSuccess.bind(this)
		this.onFail = this.onFail.bind(this)
	}

	async onSuccess(id_producto) {
		console.log(id_producto)
		const producto = await Api.getProducto(id_producto)
		this.props.producto(producto)
	}

	async onTest() {
		const id_producto = 7793433000629
		const producto = await Api.getProducto(id_producto)
		this.props.producto(producto)	
	}

	componentDidMount() {
		// this.onTest()
	}

	onFail(error) {
		alert(error.code)
	}

	render() {
		return (
			<Scanner 
				onFail={this.onFail}
				onSuccess={this.onSuccess} />
		);
	}
}

export default ProductSearch;